import { useSelector } from 'react-redux';
import React, { useState } from 'react';
import { authSelectors } from '../redux/auth';
import Navigation from './Navigation';
import Logo from './Logo';
import BurgerMenuList from './BurgerMenuList';
import ToggleIconMenu from './ToggleIconMenu';
import s from './Statefull.module.css';

export default function AppBar() {
  const isLoggedIn = useSelector(authSelectors.getIsLoggedIn);
  const [isOpen, setOpen] = useState(false);

  // –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ –∏–∫–æ–Ω–∫—É - –º–µ–Ω—é –æ–∫—Ç—Ä—ã–≤–∞–µ—Ç—Å—è/–∑–∞–∫—Ä—ã–≤–∞–µ—Ç—Å—è
  const handleIcon = () => {
    setOpen(!isOpen);
  };

  // –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å—Å—ã–ª–∫—É - –º–µ–Ω—é –∑–∞–∫—Ä—ã–≤–∞–µ—Ç—Å—è
  const closeBurgerMenu = () => {
    setOpen(false);
  };

  // –ü—Ä–∏ –∫–ª–∏–∫–µ –Ω–∞ —Å—Å—ã–ª–∫—É - —Ä–∞–∑–ª–æ–≥–∏–Ω–∏–≤–∞–µ–º—Å—è –∏ –∏–¥–µ–º –Ω–∞ –ø—É–±–ª–∏—á–Ω—ã–π —Ä–æ—É—Ç
  const LogOut = () => {
    // function logout
    setOpen(false);
  };

  return (
    <header className={s.header}>
      <Logo />
      <Navigation isLoggedIn={isLoggedIn} />
      {isOpen && (
        <BurgerMenuList
          onClick={LogOut}
          isLoggedIn={isLoggedIn}
          closeBurgerMenu={closeBurgerMenu}
        />
      )}
      <ToggleIconMenu
        isOpen={isOpen}
        isLoggedIn={isLoggedIn}
        onClick={handleIcon}
      />
    </header>
  );
}
